@import url(https://fonts.googleapis.com/css?family=Poiret+One|Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.board-center{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}



.login{
  background: #ff512f; /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #ff512f, #dd2476); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#ff512f), to(#dd2476));
  background: -webkit-linear-gradient(right, #ff512f, #dd2476);
  background: linear-gradient(to left, #ff512f, #dd2476); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.login .head{
  display: flex;
  flex-direction: row;
}

.login-box{
  margin-top: 20px;
}

.login-form{
  height:40vh;
  width: 25vw;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  background: white;
}



.login-form button{
  height: 50px;
  width: 13vw;
  margin: 5px;
  border: 1px solid rgba(128, 128, 128, 0.096);
  border-radius: 4px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  background: white;
  box-shadow: 0px 2px 4px 0px rgba(134, 53, 53, 0.342);
  -webkit-transition: box-shadow ease-in-out 0.5s;
  transition: box-shadow ease-in-out 0.5s;
}


.login-form button:hover{
  box-shadow: 0px 2px 4px 0px rgba(134, 53, 53, 0.568);
}

.login-form button img{
  width: 18px;
  height: 18px;
  margin: 10px;
}

.login-form span{
  font-family: 'Montserrat', sans-serif;
  font-size: 10px;
  font-weight: bold;
  opacity: .5;
}



.noter{
  margin-top: 40px;
  padding: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-container{
  width: 50vw;
}

.myNav{
  border-bottom: 1px solid;
  border-bottom-color: rgba(128, 128, 128, 0.24)
}

.journal{
  width: 100%;
  background: rgba(128, 128, 128, 0.062);
}

.mySection{
  width: 70vw;
  border-top: 2px rgba(128, 128, 128, 0.116) solid;
  background: rgba(128, 128, 128, 0.021);
}

.class-shelf{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.notecard{
  width: 20vw;
  margin: 8px;
  
}

.my-card{
  border-radius: 4px;
}

/* HEADER */
.my-brand{
 color: rgba(220, 20, 60, 0.164);
}

.my-button{
  color: white;
  background: #e23c136b;
  border: none;
  padding: 10px 10px;
}

.edit-button{
  background: #e23c136b;
}


/* card */
.notecard:hover{
  box-shadow: 0px 2px 4px 0px rgba(128, 128, 128, 0.377);
}
.card-footer{
  font-family: 'Roboto Mono', monospace;
  color: darkgray;
}

.card-footer a{
  text-decoration: none;
  /* color: rgb(77, 74, 74); */
  color: #00C4A7;
  font-size: 10px;
  -webkit-transition: color font-weight linear 2s;
  transition: color font-weight linear 2s;
}

.card-footer a:hover{
  color: #00C4A7;
  font-weight: 700;
}

a:nth-child(2){
  color: rgba(220, 20, 20, 0.582)
}

a:nth-child(2):hover{
  color: rgba(220, 20, 20, 0.582);
  font-weight: 700;
}

.notecard .content{
  font-family: 'Poiret One', cursive;
}

.card-header-title{
  opacity: 0.8;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

